<template>
  <div class="about">
    <div class="nijeUlogovan" v-if="!prijavljen">
    <input  type="Button" value="Почетна страница" v-on:click="redirect('home')"/><br/><br/><br/>
    <h1>Пријава</h1>
    Корисничко име:<br/>
    <input type="text" v-model="userName"/><br/>
    Лозинка:<br/>
    <input type="password" v-model="pass"/><br/><br/>
    <input type="button" value="Пријави ме" v-on:click="login()"/><br/><br/>

    <p v-on:click="redirect('register')" class="linkk">Можете такође да се региструјете кликом на ово  </p> 
    </div>
    <div v-else>
      <p>Већ сте улоговани, вратите се на почетну страницу кликом на лого сајта</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      pass:'',
      userName:''
    }
  },
  mounted(){
    let korisnik=localStorage.getItem('token');
    if(korisnik){
      this.$router.push({name:"home"})
    }
  },
  methods:{
    login(){
      if(this.pass && this.userName){
      fetch('https://vue-verification.herokuapp.com/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
        body: JSON.stringify({korisnickoIme:this.userName,lozinka:this.pass})
      }).then(res=>{
          if(res.status==400 || res.status==500){
              console.log(res.status);
              throw new Error('Pogreska');
              }
              return res.json();
          })
          .then(res=>{
            alert("Ulogovali ste se");
            localStorage.setItem('id', res.id);
            localStorage.setItem('token', res.token);
            this.$router.push({name:'home'})
          }).catch(err=>{
            console.log("Greska u unosa podataka "+err);
          });
        }
        else{
          alert('Niste popunili sva polja');
        }
    },
    redirect(argument){
      this.$router.push({name:argument})
    }

  },
  computed:{
    prijavljen(){
      return this.$store.state.prijavljen;
    }
  }
}
</script>
<style scoped>
.grid {
  display: grid;
}

.linkk{
  color:blue;
}
</style>